body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;

  line-height: 1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  width: 100%;
}

.header {
  padding: 25px 0;
  margin-bottom: 50px;
  background-color: #F8FAFA;
  position: relative;
  border-bottom: 2px solid #F2F2F2;
}

.header__rates {
  display: flex;
  justify-content: space-around;
}

.header__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 25px;
  width: 25px;
}

.main {
  margin-bottom: 200px;

}

.form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid #b9bbbb;
  padding: 50px 60px;
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 25px;
  box-shadow: 0 10px 30px #b9bbbb;
}

.form__label {
  margin-bottom: 25px;
}

.input {
  padding: 7px;
  margin-right: 10px;
  border-radius: 10px;
}

.select {
  padding: 5px;
  border-radius: 5px;
}

.btn {
  display: inline-block;
  text-decoration: none;
  padding: 5px 10px;
  color: #3674fd;
  border-color: #3674fd;
  width: 100%;
  background-color: #F6BE00;
  border-radius: 25px;
  position: relative;
  font-weight: bold;
  cursor: pointer;
}

.btn:hover {
  background-color: #8A0E0E;
}

.footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 25px 0;
  width: 100%;
  background-color: #b9bbbb;
  box-shadow: 0 -5px 50px #b9bbbb;
}

.footer__block {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer__part {
  width: 33.3333%;
}

.footer__author {
  font-weight: bold;
}

.footer__link {
  text-decoration: none;
}

.author {
  display: block;
}

@media screen and (max-width: 768px) {
  .header {
    padding: 15px 0;
  }

  .header__rates {
    flex-direction: column;
  }

  .header__rate:first-of-type {
    margin-bottom: 30px;
  }

  .input {
    margin: 0 0 6px 0;
  }

  .form {
    padding: 25px 30px;
  }

  .form__label {
    margin-bottom: 10px;
  }

  .footer {
    padding: 15px 0;
  }

  .footer__block {
    flex-direction: column;
  }

  .footer__part {
    margin-bottom: 10px;
  }

  .footer__part:last-of-type {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 576px) {
  .form {
    transform: translate(-50%, -65%);
  }
}
